import Vue from 'vue'
import VueRouter from 'vue-router'

import Inicio from '../components/Inicio.vue'
import Planetas from '../components/Planetas.vue'
import Personajes from '../components/Personajes.vue'
import Razas from '../components/Razas.vue'
import Naves from '../components/Naves.vue'
import Vehiculos from '../components/Vehiculos.vue'

Vue.use(VueRouter)

  const routes = [
  { 
    path: '/', 
    redirect: { name: 'Inicio' },
  },
  { 
    path: '/starvuers/', 
    name: 'Inicio', 
    component: Inicio
  },
  { 
    path: '/starvuers/planetas/', 
    name: 'Planetas', 
    component: Planetas
  },
  { 
    path: '/starvuers/personajes/',
    name: "Personajes", 
    component: Personajes
  },
  { 
    path: '/starvuers/razas/', 
    name: "Razas", 
    component: Razas
  },
  { 
    path: '/starvuers/naves/', 
    name: "Naves", 
    component: Naves
  },
  { 
    path: '/starvuers/vehiculos/', 
    name: "Vehiculos", 
    component: Vehiculos
  },

]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
