<template>
  <v-container fluid>
    <v-overlay :opacity="0" :value="loading">
      <v-progress-circular indeterminate size="100" color="#ffe91f">
        Loading...
      </v-progress-circular>
    </v-overlay>
    <h2 class="text-center ma-3 pa-3 heading-4 titulos">
      Planetas de Star Wars
    </h2>

    <v-data-table
      :headers="headers"
      :items="planetas"
      item-key="name"
      class="elevation-1 tabla"
      :hide-default-footer="true"
    ></v-data-table>

    <v-pagination
      circle
      color="#555555"
      class="pa-1 ma-1"
      :length="6"
      v-model="pagina"
      @input="traerPlanetas()"
      @next="traerPlanetas()"
      @previous="traerPlanetas()"
    ></v-pagination>
  </v-container>
</template>

<script>
import axios from "axios";

export default {
  name: "Planetas",
  data() {
    return {
      planetas: [],
      pagina: 1,
      cantPaginas: 1,
      loading: true,
      headers: [
        { text: "Nombre", value: "name", class: "font-weight-black title" },
        {
          text: "Population",
          value: "population",
          class: "font-weight-black title",
        },
        { text: "Climate", value: "climate", class: "font-weight-black title" },
        { text: "Terrain", value: "terrain", class: "font-weight-black title" },
        {
          text: "Diameter",
          value: "diameter",
          class: "font-weight-black title",
        },
        { text: "Gravity", value: "gravity", class: "font-weight-black title" },
        {
          text: "Surface Water",
          value: "surface_water",
          class: "font-weight-black title",
        },
        {
          text: "Rotation Period",
          value: "rotation_period",
          class: "font-weight-black title",
        },
        {
          text: "Orbital Period",
          value: "orbital_period",
          class: "font-weight-black title",
        },
      ],
    };
  },
  methods: {
    async traerPlanetas() {
      const params = {
        page: this.pagina,
      };
      this.loading = true;
      await axios
        .get("https://swapi.dev/api/planets/", { params })
        .then((res) => {
          this.planetas = res.data.results;
          this.cantPaginas = res.data.count / 10;
          this.loading = false;
        })
        .catch(function (error) {
          alert("Hubo un error en la peticion");
        });
    },
  },
  created() {
    this.traerPlanetas();
  },
};
</script>

<style>
.v-data-table-header th {
  white-space: nowrap;
  background-color: #ffe91f;
  border-right: 1px solid black;
}

.v-data-table td {
  border-right: 1px solid black;
}

.v-data-table tr {
  background-color: #ccc;
  cursor: pointer;
}

.titulos {
  color: #cabd41;
  text-shadow: 3px 3px 5px white;
  font-size: 40px;
}
</style>