<template>
  <v-container>
    <v-overlay :opacity="0" :value="loading">
      <v-progress-circular indeterminate size="100" color="#ffe91f">
        Loading...
      </v-progress-circular>
    </v-overlay>
    <h2 class="text-center ma-3 pa-3 heading-4 titulos">
      Vehiculos de Star Wars
    </h2>

    <v-row>
      <v-col cols="12">
        <v-expansion-panels hover dark>
          <v-expansion-panel
            v-for="vehiculo in vehiculos"
            :key="vehiculo.name"
            class="panel"
          >
            <v-expansion-panel-header class="titulos">{{
              vehiculo.name
            }}</v-expansion-panel-header>
            <v-expansion-panel-content color="#555555">
              <strong>Model:</strong>
              {{ vehiculo.model }}
              <br />
              <strong>Manufacturer:</strong>
              {{ vehiculo.manufacturer }}
              <br />
              <strong>Class:</strong>
              {{ vehiculo.vehicle_class }}
              <br />
              <strong>Length:</strong>
              {{ vehiculo.length }}
              <br />
              <strong>Crew:</strong>
              {{ vehiculo.crew }}
              <br />
              <strong>Passengers:</strong>
              {{ vehiculo.passengers }}
              <br />
              <strong>Cargo capacity:</strong>
              {{ vehiculo.cargo_capacity }}
              <br />
              <strong>Cost in credits:</strong>
              {{ vehiculo.cost_in_credits }}
              <br />
              <strong>Max Speed:</strong>
              {{ vehiculo.max_atmosphering_speed }}
              <br />
            </v-expansion-panel-content>
          </v-expansion-panel>
        </v-expansion-panels>
      </v-col>
    </v-row>

    <v-pagination
      circle
      color="#555555"
      :length="4"
      v-model="pagina"
      @input="traerVehiculos()"
      @next="traerVehiculos()"
      @previous="traerVehiculos()"
    ></v-pagination>
  </v-container>
</template>
<script>
import axios from "axios";

export default {
  name: "Vehiculos",
  data() {
    return {
      vehiculos: [],
      pagina: 1,
      cantPaginas: 1,
      loading: true
    };
  },
  methods: {
    async traerVehiculos() {
      const params = {
        page: this.pagina,
      };
      this.loading = true;
      await axios
        .get("https://swapi.dev/api/vehicles/", { params })
        .then((res) => {
          this.vehiculos = res.data.results;
          this.cantPaginas = res.data.count / 10 + 1;
          this.loading = false;
        })
        .catch(function (error) {
          alert("Hubo un error en la peticion");
        });
    },
  },
  created() {
    this.traerVehiculos();
  },
};
</script>
<style>
.panel {
  color: white !important;
}
</style>