<template>
  <v-app class="app">
    <Header></Header>

    <v-main>
      <v-overlay :opacity="0" :value="loading">
        <v-progress-circular indeterminate size="100" color="#ffe91f">
          Loading...
        </v-progress-circular>
      </v-overlay>

      <router-view></router-view>
    </v-main>

    <Footer></Footer>
  </v-app>
</template>

<script>
import Inicio from "./components/Inicio";
import Header from "@/components/Header";
import Footer from "@/components/Footer";

import axios from "axios";
import store from "@/store";

export default {
  name: "App",
  store,
  components: {
    Header,
    Inicio,
    Footer,
  },
  data() {
    return {
      loading: true,
    };
  },

  async created() {
    await axios
      .get("https://swapi.dev/api/films/")
      .then((res) => {
        this.$store.commit("agregarPeliculas", res.data.results);
        this.loading = false;
      })
      .catch(function (error) {
        alert("Hubo un error en la peticion");
      });
  },
};
</script>

<style>
.app {
  background-color: black !important;
}
</style>
